* {
  margin: 0;
}

body {
  margin: 0;
  box-sizing: border-box;
  font-size: 16px;
  line-height: 2rem;
  font-family: serif;
  width: 100vw;

  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;

  grid-template-areas:
    "header"
    "nav"
    "dash";
}
